require('../sass/news.scss');
(function () {
    var listAll = [];
    var vm = new Vue({
        el: '#main',
        data: {
            listData: listAll
        },
        components: {
            'c-footer': require('../components/c-footer'),
            'c-header': require('../components/c-header')
        }
    });
    $.ajax({
        type: 'GET',
        url: 'noMD5/news.json',
        data: {},
        dataType: 'json',
        timeout: 2000,
        success: function (data, status, xhr) {
            vm.listData = data;
        },
        error: function (xhr, type, error) {
        }
    });
})();


